//     //1'parent':offsetParent
//     //2Dom
//     //3{ left: number, top: number, right: number, bottom: number }
// canceldrag'.body'
// defaultClassNamestring // ui'react-draggable'
// drfaultClassNameDraggingstring // ui'eact-draggable-dragging'
// defaultClassNameDraggedstring //'react-draggable-dragged'
// defaultPosition{ x: number, y: number } // xy
// disabledboolean // true
// grid[number, number] // 
// handlestring // '.handle'
// offsetParentHTMLElement // offsetParent
// onMouseDown: (e: MouseEvent) => void // 
// onStart: DraggableEventHandler // 
// onDragDraggableEventHandler // 
// onStopDraggableEventHandler // 
// position: {x: number, y: number} // 
// positionOffset: {x: number | string, y: number | string} // 
// scalenumber // 

import React, { useEffect, useRef, useState } from "react";
import Draggable from "react-draggable"; 
import "./b.less";

const B: React.FC = () => {
  const myRef: any = useRef(null);
  const [width, setWidth] = useState<number>(0);
  const [height, setHeight] = useState<number>(0);
  console.log("myRef", myRef.current);

  useEffect(() => {
    const doms: any = document.getElementsByClassName("box");
    if (!myRef || !doms) return;
    console.log("myRef", myRef, myRef.current.offsetWidth, myRef.current.offsetHeight, doms, doms[0]);
    setWidth(myRef.current.offsetWidth - doms[0].offsetWidth);
    setHeight(myRef.current.offsetHeight - doms[0].offsetWidth);
  }, []);

  const Box = (a: string, b: string) => {
    return <div className="box">
      <div className='drag-handler'>{a}</div>
      <div>{b}</div>
    </div>;
  };

  return <div className="page-content" ref={myRef}>
    <Draggable 
      handle=".drag-handler"
      bounds={{ left: 20, right: width - 20, top: 20, bottom: height - 20 }}
      defaultPosition={{ x: 20, y: 20 }}
    >
      <div>{Box("", "")}</div>
    </Draggable>
  </div>;
};

export default B;
